.App {
  text-align: center;
}

#App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  #App-logo {
    animation: App-logo-spin infinite 0.1s linear;
  }
}

#Instructions button {
  background-color: #00d4ff;
  color: black;
  font-size: 1.5em;
  margin: 1em;
  padding: 0.25em 1em;
  border-radius: 4px;
  border: 3px solid rgba(31,31,79,1);

 
}

#Instructions button:hover {
  box-shadow: 0 12px 16px 0 rgba(31,31,79,1),0 17px 50px 0 rgba(31,31,79,1);

}

.App-header {
  background-color: white;

  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
  font-family: Glober W01 Regular;
}
/* 0c3b6c*/
#Bank{
  background-color: #AC266B;
  width:100%;
  border: 2px solid black;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 2%;
}

#Bank input {
  width: 50%;
  height: 5vh;
  margin: 1em;
  padding: 0.25em 1em;
  border: 2px solid #61dafb;
  border-radius: 3px;
}
#Bank button {
  background-color: #00d4ff;
  color: black;
  font-size: 1.5em;
  margin: 1em;
  padding: 0.25em 1em;
  border: 3px solid rgba(31,31,79,1);
  border-radius: 4px;

 
}

#Bank button:hover {
  box-shadow: 0 12px 16px 0 rgba(31,31,79,1),0 17px 50px 0 rgba(31,31,79,1);

}

#StockMarketTable {
  width: 100%;
  border: 2px solid black;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#StockMarketTable h1 {
  color: black;
}
/* 0c3b6c */
nav {
  width: 100%;
  overflow: hidden;
  position: fixed;
  width: 100%;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: row;
  padding-left: 0%;
  font-family: "Glober W01 SemiBold";
  background: #1F1F4F;
  background: linear-gradient(90deg, rgba(31,31,79,1) 50%, rgba(0,212,255,1) 100%);}

#nav-list {
  list-style-type: none;
  width:  60%;
}
#nav-list li {
  float: left;
  position: relative;
  display: block;
  color: rgba(0,212,255,1);
  padding-left: 2%;
  padding-right: 2%;
  text-align: right;
}


#navDay {
 color: rgba(31,31,79,1);
 padding-left: 20%;
 font-weight: bold;
}
/*White: f2f2f2 */
#nav-list li a {
  text-decoration: none;
  color:rgba(0,212,255,1);
}

nav p {
  margin-left: 3%;
}



#Buy-AND-Sell{
  background-color: #A750A3;
  width:100%;
  border: 2px solid black;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 2%;
}

#buy-and-sell-buttons {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

#Buy-AND-Sell select, input{
  width: 50%;
  height: 5vh;
  margin: 1em;
  padding: 0.25em 1em;
  border: 2px solid #61dafb;
  border-radius: 3px;
}


#cashDisplay {
  display: flex;
  align-items: left;
}

#profit {
  color: darkgreen;
  font-weight: bold;
}

#loss {
  color: red;
  font-weight: bold;
}

.Buy-button {

  color: black;
  font-size: 1.5em;
  margin: 1em;
  padding: 0.25em 1em;
  border: 2px solid rgba(0,212,255,1);
  border-radius: 3px;
  background-color: #00cc00;
 
}

.Sell-button {
  background-color: red;
  color: black;
  font-size: 1.5em;
  margin: 1em;
  padding: 0.25em 1em;
  border: 2px solid rgba(0,212,255,1);
  border-radius: 3px;
} 

button:hover {
  background-color: rgba(0,212,255,1);
  box-shadow: 0 12px 16px 0 rgba(31,31,79,1),0 17px 50px 0 rgba(31,31,79,1);

}

footer {
  background-color: #1F1F4F;
  color: white;
  padding: 15px;
  text-align: center;
  position: relative;
  left: 0;
  bottom: 0;
  width: 98%;
  background: linear-gradient(90deg, rgba(31,31,79,1) 50%, rgba(0,212,255,1) 100%);}

#footer p {
  font-size: 0.5em;
}



table, td, th {  
  border: 2px solid black;
  text-align: center;
}

table {
  border-collapse: collapse;
  width: 100%;
  color: black;
  background-color: #8E76D1 ;


}

th, td {
  padding: 10px;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


#dropdown {
  position: fixed;
  display: inline-block;
}

#dropdown-content {
  display: none;
  position: relative;
  color: white;
  max-width: 150%;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  padding: 12px 16px;
  z-index: 1;
  text-align: center;
  background: linear-gradient(180deg, rgba(31,31,79,1) 37%, rgba(19,102,148,1) 61%, rgba(11,146,191,1) 82%, rgba(0,212,255,0.8) 100%);
}

#dropdown:hover #dropdown-content {
  display: block;
}
#opening {
  width: 100%;
}
#GameOver {
  width:100%;
}